@-webkit-keyframes slide-in {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slide-in {
  from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slide-out {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes slide-out {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@-webkit-keyframes screen-darken {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.4;
  }
}

@keyframes screen-darken {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.4;
  }
}

@-webkit-keyframes screen-lighten {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 0;
  }
}

@keyframes screen-lighten {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 0;
  }
}

.slide-in-animation {
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-name: slide-in;
          animation-name: slide-in;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.slide-out-animation {
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-name: slide-out;
          animation-name: slide-out;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.darken-animation {
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-name: screen-darken;
          animation-name: screen-darken;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.lighten-animation {
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  -webkit-animation-name: screen-lighten;
          animation-name: screen-lighten;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

